body{
  padding: 0;
  margin: 0;
}


#root {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50rem;
  height: 50rem;

}


.grid{
  display: grid;
  grid-template-columns: auto auto auto;
  grid-template-rows: auto auto auto;
  column-gap: 10px;
  row-gap: 10px;
  background-color: rgb(26, 48, 126);
}

.square{
  width: 110px;
  height: 110px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: white;
}

.circle{
  font-size: xxx-large;
}

.cross{
  stroke: #ec088e;
}

svg{
  stroke-width: 15px;
  stroke: #214cb8;
  fill:none;
  stroke-linecap: round;
}
.result{
  position: absolute;
  top: 0; 
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center; 
  justify-content: center;
  flex-direction: column;
  font-size: 3em;
  font-weight: bold;
  background-color: rgba(0, 0, 0, 0.4);
  color: white;
}

button{
  padding: 10px;
  font-size: 0.5em;
}